export default {
	data() {
		return {
			searchVal: '',
			searchTeamList:[],
			joinTeamList:[],
			page:0
		};
	},
	onLoad(){
		this.userInfo = uni.getStorageSync('userInfo');
		this.loadData();
	},
	//下拉刷新
	onPullDownRefresh() {
	    this.loadData("refresh");
	},
	//加载更多
	onReachBottom() {
	    this.loadData('more');
	},
	methods:{
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = "add", loading) {
			//没有更多直接返回
			if (type === "add") {
			    if (this.loadingType === "nomore") {
			        return;
			    }
			    this.loadingType = "loading";
			} else {
			    this.loadingType = "more";
			}
			if (type === "refresh") {
				this.joinTeamList=[];
			    this.page = 1;
			}
		    uni.request({
		        url: this.$Url + "/clmsj/Team/supplierList ",
		        header: {  auth: uni.getStorageSync("session_key")  },
		        data: {
		            page: this.page
		        },
		        method: "POST",
		        success: res => {			
		            if (res.data.code == 0) {
		                let teamList = res.data.data;
		                this.page++;
		                if (type == "refresh") {
		                    this.joinTeamList = teamList;
		                } else {
		                    this.joinTeamList = this.joinTeamList.concat(teamList);
		                }
		
		                this.loadingType = teamList.length < 10 ? "nomore" : "more";
		                if (type === "refresh") {
		                    if (loading == 1) {
		                        uni.hideLoading();
		                    } else {
		                        uni.stopPullDownRefresh();
		                    }
		                }
		            }
		        }
		    });
		},
		quitTeam(item,key){
			let _this = this;
			uni.showModal({
			    title: '提示',
			    content: '你确认要退出该批发团队吗？',
			    success: function (res) {
			        if (res.confirm) {
			            uni.request({
			            	url: _this.$Url + '/clmsj/team/outStock', // http://bull.hnw.net/index/bull/client_wash_info
			            	data: { id:item.id },
			            	header:{ 'auth' : uni.getStorageSync('session_key') },
			            	method:'POST',
			            	success: (res) => {
			            		if (res.data.code == 0) {
			            			_this.$api.msg('退出成功');							
									_this.loadData();
			            		}else{
			            			_this.$api.msg(`退出失败！` + res.data.msg);
			            		}
			            	},
			            })
			        }
			    }
			});
			
		},
		searchTeam(e){
			let _this = this;
			uni.request({
				url: _this.$Url + '/clmsj/Team/searchTeam', // http://bull.hnw.net/index/bull/client_wash_info
				data: { 'id' : _this.searchVal},
				header:{
					'auth' : uni.getStorageSync('session_key')
				},
				method:'POST',
				success: (res) => {
					if (res.data.code == 0) {
						_this.searchTeamList = res.data.data.team;
					}
				},
			})
		},
		joinTeam(item){
			let _this = this;
			uni.request({
				url: _this.$Url + '/clmsj/Team/applyStock', // http://bull.hnw.net/index/bull/client_wash_info
				data: { 'team_id' : item.id},
				header:{
					'auth' : uni.getStorageSync('session_key')
				},
				method:'POST',
				success: (res) => {
					if (res.data.code == 0) {
						_this.$api.msg('申请成功，请等待团队主认证');
						// this.loadData();
						uni.navigateBack();
						console.log(res)
					}else{
						_this.$api.msg(res.data.msg);
						setTimeout(()=>{
							uni.navigateBack()
						},1500)
					}
				},
			})
		},
	}
}
